.contact {
  @include pageGrid;

  &__form {
    grid-column: 1 / -1;

    &-input {
      outline: none;
      padding-left: 16px;
      box-sizing: border-box;
      border: 1px solid #fff;
      font-size: 14px;
      width: 100%;
      height: 48px;
      margin-bottom: 16px;
      border-radius: 40px;
    }

    &-textarea {
      outline: none;
      font-family: Poppins, sans-serif;
      resize: none;
      box-sizing: border-box;
      padding-top: 14px;
      padding-left: 16px;
      border: 1px solid #fff;
      font-size: 14px;
      width: 100%;
      margin-bottom: 16px;
      border-radius: 15px;
    }

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__content {
    grid-column: 1 / -1;

    &--list {
      padding: 0;
      list-style: none;
    }

    &--item {
      padding-bottom: 24px;

      &-title {
        font-size: 14px;
        line-height: 140%;
      }

      &-link {
        text-decoration: none;
        color: #fff;
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;

        @include hover(color, grey);
      }
    }

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: -6 / -1;
    }
  }
}
